.selected-menu{
  background: var(--root-selectr)
}

.button-10 {
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6px 14px;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', sans-serif;
  border-radius: 6px;
  border: none;
  width: 80px;
  margin-left: 5px;

  color: #fff;
  background: linear-gradient(180deg, #4B91F7 0%, #367AF6 100%);
  background-origin: border-box;
  box-shadow: 0 1px 2px rgba(54, 122, 246, 0.25), inset 0 1px 0 -0.25px rgba(255, 255, 255, 0.2);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}
.button-10:hover{
  background: linear-gradient(180deg, #69a4fa 0%, #4877cb 100%);
}

.button-10:focus {
  box-shadow: inset 0 1px 0 -0.25px rgba(255, 255, 255, 0.2), 0 1px 2px rgba(54, 122, 246, 0.25), 0 0 0 4px rgba(58, 108, 217, 0.5);
  outline: 0;
}
/*----------------alert--------------*/

.main-dialog-alert {
  position: absolute;
  margin: 0;
  top: 20px;
  left: 20px;
  padding: 0;


  width: 400px;
  height: fit-content;
  z-index: 2;
  background: white;
  border: thick #fdfdfd;

  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.19) 0 10px 20px, rgba(0, 0, 0, 0.23) 0 6px 6px;
}
.m-body-alert{

  background: rgba(22, 231, 33, 0.32);

}
.body-test{
  padding: 10px;
  display: flex;
  align-items: center;
  height: fit-content;
}
/*--------------register----------------*/

.App {
  display: flex;
  flex: 1;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

fieldset {
  display: flex;
  width: 480px;
  padding: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex-direction: column;
}

label {
  margin-bottom: 8px;
  font-size: 20px;
}

sup {
  color: red;
  top: -2px;
  font-size: 95%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

input,
select {
  border-radius: 4px;
  border: 1px solid #ccc;
  height: 32px;
  padding: 0 8px;
}

.Field {
  position: relative;
  padding-bottom: 24px;
  display: flex;
  flex-direction: column;
}

.FieldError {
  position: absolute;
  bottom: -6px;
  color: red;
  font-size: 12px;
}

.Row {
  display: flex;
  flex-direction: row;
}

button {
  margin-right: 5px;
  padding: 5px 15px 5px 15px;
  width: fit-content;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: #e8e8f6;
  color: #100f0f;

  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
}

button:disabled,
button[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
}
/*----------------------------------*/
.show-all-menu{
  border-radius: 30%;
  position: absolute;
  padding: 10px 10px 5px 10px;
  top:10px;
  right: 20px;
  cursor: pointer;
}
.show-all-menu:hover{
  background: #dddfe1;
}
.show-all-menu:active{
  background: #d2d4d7;
}



